import * as React from 'react';
import Embed from './embed';
import { UploadOrFailedCard } from '@ali/4ever-factory';
import { BiPluginConfig } from '../../../types';

export default function createRenderNode(config: BiPluginConfig) {
  const embedConfig = config.embed || {};
  const {
    locale: embedLocaleConfig,
    downloadFile,
    onPreview,
    enableFilePreview = false,
  } = embedConfig;

  return {
    embed: (props, controller, next) => {
      const { node } = props;
      if (!node.data.src) {
        return <UploadOrFailedCard {...props} node={node} locale={embedLocaleConfig} controller={controller} />;
      }
  
      if (node.data.type !== 'video' && node.data.type !== 'audio') {
        return (
          <Embed
            {...props}
            controller={controller}
            onDownload={downloadFile}
            onPreview={onPreview}
            locale={embedLocaleConfig}
            enableFilePreview={enableFilePreview}
          />
        );
      }
      return next();
    },
  };
}
